<template>
  <div class="main">
    <div class="draw-area">
      <div class="stage" id="stage">
        <div class="standard">
          <div class="btn-reset" v-on:click="reset()">리셋</div>
        </div>

        <div class="pillar" id="pillar_12" v-on:click="selectPillar(12)"></div>
        <div class="pillar" id="pillar_13" v-on:click="selectPillar(13)"></div>
        <div class="pillar" id="pillar_15" v-on:click="selectPillar(15)"></div>
        <div class="pillar" id="pillar_17" v-on:click="selectPillar(17)"></div>
        <div class="pillar" id="pillar_18" v-on:click="selectPillar(18)"></div>
        <div class="pillar" id="pillar_19" v-on:click="selectPillar(19)"></div>
        <div class="pillar" id="pillar_21" v-on:click="selectPillar(21)"></div>
        <div class="pillar" id="pillar_23" v-on:click="selectPillar(23)"></div>
      </div>
    </div>
    <div class="control-area">
      <div class="rotate rotate_90" v-on:click="rotate(90)">
        90º
      </div>
      <div class="rotate rotate_180" v-on:click="rotate(180)">
        180º
      </div>
      <div class="rotate rotate_270" v-on:click="rotate(270)">
        270º
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MainPage',
  data() {
    return {
      selected: [], 
      pillarColor: 'yellow'
    }
  }, 
  methods: {
    selectPillar(idx) {
      if (this.selected.length > 4) {
        return false;
      }
      else {
        this.selected.push(idx);
        const pillar = document.getElementById(`pillar_${idx}`);
        pillar.style.backgroundColor = this.pillarColor;
      }
    }, 
    rotate(deg) {
      const stage = document.getElementById('stage');
      stage.style.transform = `translate(-50%, -50%) rotate(0deg)`;
      stage.style.transform = `translate(-50%, -50%) rotate(${deg}deg)`;
    }, 
    reset() {
      const stage = document.getElementById('stage');
      stage.style.transform = `translate(-50%, -50%) rotate(0deg)`;
      for (var i=0; i < this.selected.length; i++) {
        const pillar = document.getElementById(`pillar_${this.selected[i]}`);
        pillar.style.backgroundColor = 'white';
      }
      this.selected = [];
    }, 
  }
}
</script>

